<template>
<div>
<div id="hostmap" :style="style"></div>
</div>
</template>
<script>
export default{
    data(){
      return{
        style:{
          width:'100%',
          height:'100%',
          position:'absolute',
        }
       }

    },
  mounted(){
// 全局钩子
 var vueThis=this;
 var hostmap=document.getElementById('hostmap');
 hostmap.style.width=window.innerWidth+'px';
 hostmap.style.height=window.innerHeight+'px';
var myChart = echarts.init(hostmap);
var geoCoordMap ={
 '节点3':[124.410404,31.16982],
 '节点5':[124.399616,31.172258],
 '节点8':[124.405324,31.172052],
 '节点2':[124.409445,31.177932],
 '节点10':[124.404895,31.177335],
 '节点0':[124.407503,31.176499],
 '节点9':[124.404866,31.174181],
 '节点7':[124.409271,31.176429],
 '节点4':[124.409315,31.179581],
 '节点1':[124.406648,31.182804],
 '节点6':[124.412265,31.173405]

};
var Data =[
    [{name:'节点0'},{name:'节点3',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点5',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点8',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点2',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点10',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点9',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点7',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点4',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点1',value:90,color:'#03FED2'}],
    [{name:'节点0'},{name:'节点6',value:90,color:'#03FED2'}]
]
var convertData = function (data) {
    var res = [];
    for (var i = 0; i < data.length; i++) {
        var dataItem = data[i];
        var fromCoord = geoCoordMap[dataItem[1].name];
        var toCoord = geoCoordMap[dataItem[0].name];
        if (fromCoord && toCoord) {
            res.push({
                fromName: dataItem[0].name,
                toName: dataItem[1].name,
                coords: [fromCoord, toCoord]
            });
        }
    }
    return res;
};

var series = [];
[['节点0', Data]].forEach(function (item,i) {
    var color='';
    item[1].map(function (dataItem) {
      // console.log(dataItem[1].color);
    })
    series.push({
            name: item[0],
            type: 'lines',
            coordinateSystem: 'bmap',
            zlevel: 1,
            // 动线样式
            effect: {
                show: true,
                period:7,
                trailLength:0.8,
                color:'#03FED2',
                symbolSize:2,
            },
            // 实线样式
            lineStyle: {
                normal: {
                    color:'#03FED2',
                    width: 0,
                    curveness:0.2
                }
            },
            data: convertData(item[1])
        },
        {
            name: item[0],
            type: 'lines',
            coordinateSystem: 'bmap',
            zlevel: 2,
            effect: {
                show: true,
                period: 6,
                trailLength: 0,

            },
            lineStyle: {
                normal: {
                    color:'#03FED2',
                    width:1,
                    opacity: 0.4,
                    curveness: 0.2
                }
            },
            data: convertData(item[1])
        },
        {
            name:'详细信息',
            type: 'effectScatter',
            coordinateSystem: 'bmap',
            zlevel: 2,
            rippleEffect: {
                brushType: 'stroke'
            },
            label: {
                normal: {
                    show: true,
                    position: 'right',
                    formatter: '{b}'
                }
            },
            symbolSize: function (val) {
                return val[2] / 5;
            },
             data: item[1].map(function (dataItem) {
                return {
                    name: dataItem[1].name,
                    value: geoCoordMap[dataItem[1].name].concat([dataItem[1].value]),
                     itemStyle: {
                normal:{
                color:dataItem[1].color
                }
            },
                };
            })
        });
});

var option = {
    tooltip : {
        trigger: 'item',
        position:['10%' ,'10%'],
        formatter:function(params)
            {
                console.log(params);
                var relVal = params.name+ params.seriesName;
                relVal += '<div  ><span></span>'+ '经度' + ' : ' + params.value[0]+"</div>";
                relVal += '<div  ><span></span>'+ '纬度' + ' : ' + params.value[1]+"</div>";
                relVal += '<div  ><span></span>'+ '当前海浪高度' + ' : ' +"</div>";
                return relVal;
            }

    },
    bmap: {
        roam: true,
        silent:true,
        mapStyle:{
           styleJson: [
                {
                  'featureType': 'water',
                  'elementType': 'all',
                  'stylers': {
                    'color': '#031628'
                  }
                },
                {
                  'featureType': 'land',
                  'elementType': 'geometry',
                  'stylers': {
                    'color': '#000102'
                  }
                },
                {
                  'featureType': 'highway',
                  'elementType': 'all',
                  'stylers': {
                    'visibility': 'off'
                  }
                },
                {
                  'featureType': 'arterial',
                  'elementType': 'geometry.fill',
                  'stylers': {
                    'color': '#000000'
                  }
                },
                {
                  'featureType': 'arterial',
                  'elementType': 'geometry.stroke',
                  'stylers': {
                    'color': '#0b3d51'
                  }
                },
                {
                  'featureType': 'local',
                  'elementType': 'geometry',
                  'stylers': {
                    'color': '#000000'
                  }
                },
                {
                  'featureType': 'railway',
                  'elementType': 'geometry.fill',
                  'stylers': {
                    'color': '#000000'
                  }
                },
                {
                  'featureType': 'railway',
                  'elementType': 'geometry.stroke',
                  'stylers': {
                    'color': '#08304b'
                  }
                },
                {
                  'featureType': 'subway',
                  'elementType': 'geometry',
                  'stylers': {
                    'lightness': -70
                  }
                },
                {
                  'featureType': 'building',
                  'elementType': 'geometry.fill',
                  'stylers': {
                    'color': '#000000'
                  }
                },
                {
                  'featureType': 'all',
                  'elementType': 'labels.text.fill',
                  'stylers': {
                    'color': '#857f7f'
                  }
                },
                {
                  'featureType': 'all',
                  'elementType': 'labels.text.stroke',
                  'stylers': {
                    'color': '#000000'
                  }
                },
                {
                  'featureType': 'building',
                  'elementType': 'geometry',
                  'stylers': {
                    'color': '#022338'
                  }
                },
                {
                  'featureType': 'green',
                  'elementType': 'geometry',
                  'stylers': {
                    'color': '#062032'
                  }
                },
                {
                  'featureType': 'boundary',
                  'elementType': 'all',
                  'stylers': {
                    'color': '#465b6c'
                  }
                },
                {
                  'featureType': 'manmade',
                  'elementType': 'all',
                  'stylers': {
                    'color': '#022338'
                  }
                },
                {
                  'featureType': 'label',
                  'elementType': 'all',
                  'stylers': {
                    'visibility': 'off'
                  }
                },
                {
                    "featureType": "poi",
                    "elementType": "all",
                    "stylers": {
                              "color": "#03FED2",
                              "visibility": "off"
                    }
          }

              ]
        }
    },
    series: series
};

myChart.setOption(option);
var map = myChart.getModel().getComponent('bmap').getBMap();
    map.disableDoubleClickZoom();
      map.centerAndZoom(new BMap.Point(124.407503,31.176499),17);   // 初始化地图


  }

  }
</script>
<style scoped>


</style>



